@import '../../Helpers//Helpers.scss';

.pagination {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;

  &__list {
    display: flex;
    list-style: none;
    flex-wrap: wrap;
    gap: 5px;
    padding: 0;
    margin: 0;
  }

  &__item {
    border: 1px solid $grey-lite;
    margin: 0;
    padding: 5px 10px;
    color: $black;
    text-decoration: none;

      &--active {
        background-color:$black;
        color: $white;
      }
    }

  &__button {
    border: 1px solid $grey-lite;
    padding: 5px 10px;

    &--disabled {
      pointer-events: none;
      background-color: #eee;
    }
  }
}