@import '../../Helpers/Helpers.scss';

.query {
//   min-width: 250px;
  display: none;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  border: 1px solid $grey-lite;
  padding: 0 5px;


    @media (min-width: 820px) {
        display: flex;
    }

  &__input {
    border: 0px;
    width: 100%;
    padding: 5px;
    outline: none;

    &::placeholder {
        font-size: 13px;
        letter-spacing: -0.5px;
        font-weight: 500;
        color: $grey-dark;
    }
  }

  &__img {
    width: 15px;
    height: 15px;
  }
}