// @import '../../Helpers.scss';
@import '../../Helpers/Helpers.scss';


.slider {
  width: 250px;
  height: 300px;
  margin: 0;
  margin: 0 auto;

  @media (min-width: 380px) {
    width: 350px;
  }

  @include tablet() {
    width: 600px;
  }

  @include desktop() {
    width: 800px;
  }

  &__container {
    display: flex;
    justify-content: center;
  }

  &__button {
    border: 1px solid $grey-medium;
    background-color: transparent;

    &--active {
      background-color: $grey-lite;
    }
  }

  &__items {
    display: flex;
    height: 300px;
  }

  &__img {
    min-width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  &__wrapper {
    height:  100%;
    display: flex;
    gap: 20px;
    height: 300px;
    width: 750px;
    overflow: hidden;
    margin: 0 auto;
  }

  &__dots {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }


  &__dot {
    width: 14px;
    height: 4px;
    border: 0px;
    background-color: $grey-lite;

    &--active {
      background-color: $black;
    }
  }
}